<template>
    <div id="background">
        <div id="clock">
            <div id="time">
                {{time}}
            </div>
            <div id="date">
                {{date}}
            </div>
        </div>
        <div id="logo">软件杯</div>
        <div id="login">
            <el-card class="box-card" shadow="always">
                <div id="form">
                    <div>LOGIN</div>
                    <div>欢迎来到故障检测系统</div>
                    <el-button type="primary" @click="systemjob()">进入系统</el-button>
                </div>
                
            </el-card>
        </div>
        
        <div id="copyright">copyright @ 2023 软件杯-汪汪队 All Right Reserved.</div>
    </div>
</template>
<style scoped>
#background{
    background-color: rgba(0, 238, 255, 0.64);
    position: fixed;
    height: 100%;
    width: 100%;
    top: 0;
    left: 0;
    background-image: linear-gradient(
        90deg,
        cyan,
        purple
    );
    background-size: 400%;
    animation: myanimation 10s infinite;
}
@keyframes myanimation {
    0%{
        background-position: 0% 50%;
    }
    50%{
        background-position: 100% 50%;
    }
    100%{
        background-position: 0% 50%;
    }
}
#logo{
    color: aliceblue;
    font-size: 25px;
    font-weight: 800;
    position: absolute;
    top: 15%;
    left: 15%;


}
#login{
    font-size: 50px;
    font-weight: 50;
    position: absolute;
    top: 30%;
    left: 40%;
}
.el-card{
    width: 700px;
    height: 500px;
    background-color: rgba(255, 255,2555, 0.4);
    border-radius: 30px;
}
#form{
    line-height: 150px;
}
#clock{
    color: aliceblue;
    position: absolute;
    top: 70%;
    left: 15%;
    text-align: left;
}
#time{
    font-size: 100px;
}
#date{
    font-size: 75px;
}
.el-button {
    display: inline-block;
    line-height: 1;
    white-space: nowrap;
    cursor: pointer;
    background: #409EFF;
    border: 1px solid #DCDFE6;
    color: #606266;
    -webkit-appearance: none;
    text-align: center;
    box-sizing: border-box;
    outline: 0;
    margin: 0;
    transition: .1s;
    font-weight: 500;
    padding: 12px 20px;
    font-size: 30px;
    border-radius: 4px;
    bottom: 10%;
}  
#copyright{
    color: aliceblue;
    position: absolute;
    top: 95%;
    left: 45%;
    font-size: 20px;
} 
</style>
<script>
export default{
    data(){
        return{
            time:"",
            date:"",
        }
    },
    mounted(){
        this.$nextTick(()=>{
            setInterval(this.update_clock,10)
        }

        )
    },
    methods:{
        update_clock:function(){
            var d=new Date();
            var year=d.getFullYear();
            if(year<10){
                year="0"+year;
            }
            var day=d.getDate();
            if(day<10){
                day="0"+day;
            }
            var mon=d.getMonth()+1;
            if(mon<10){
                mon="0"+mon;
            }
            var hour=d.getHours();
            if(hour<10){
                hour="0"+hour;
            }
            var minute=d.getMinutes();
            if(minute<10){
                minute="0"+minute;
            }
            this.time=hour+":"+minute;
            this.date=year+":"+mon+":"+day;

        },
        systemjob(){
            this.$router.push('/systemjob')
            this.$message({
                showClose: true,
                message: 'Welcome',
                type: 'success'
            });
        }
    }
}
</script>
