import Vue from 'vue'
import VueRouter from 'vue-router'
import HomeView from '../views/HomeView.vue'
import login from '../views/login.vue'
import systemjob from '../components/systemjob.vue'
import XLSX from 'xlsx';
import Django_test from '../components/Django_test.vue'
import Axios from 'axios'
import axios from 'axios';
Vue.prototype.$axios = axios;
Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'login',
    component: login
  },
  {
    path: '/systemjob',
    name: 'systemjob',
    component: systemjob
  },
  {
    path: '/Django_test',
    name: 'Django_test',
    component: Django_test
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
export function postUpload (file,URL) {
  return axios({
      url: URL,
      method: 'post',
      data: file,
      headers: {
          'Content-Type': 'multipart/form-data'
      }
  })
}