<template>

  <!--背景色-->
  <div id="background">
    <div id="logo">软件杯</div>
    <div id="clock">
            <div id="time">
                {{time}}
            </div>
            <div id="date">
                {{date}}
            </div>
        </div>
    <!-- 背景隔板-->
    <div id="card">
      <el-card class="box-card">
        <!--图片卡格-->
        <el-card class="picture-card">
          <!--图标-->

          <!--显示图片区-->
          <div class="demo-image__lazy">
                     
            <el-image v-for="url in urls" :key="url" :src="url" lazy></el-image>
                   
          </div>
        </el-card>
        <div class="container">
          <!--训练-->
          <div class="train">
            <span id="knn">KNN数据填充参数：</span>
            <span id="moxing">模型训练参数：</span>
            <span
              style="color: #606266; top: 50%; left: 10%; position: absolute"
              >上传训练集文件:</span
            >
            <el-tag type="success">训练</el-tag>
            <!-- 文件上传-->
            <!--文件上传-->
            <el-upload
              style="position: absolute; left: 25%; top: 49%"
              action="https://jsonplaceholder.typicode.com/posts/"
              :http-request="onUpload1"
            >
              <el-button size="small" type="primary">点击上传</el-button>
            </el-upload>
            <form action=""></form>
            <el-input
              placeholder="请输入k值"
              v-model="input"
              clearable
              style="width: 20%; left: 46%; top: 20%; position: absolute"
            >
            </el-input>

            <el-input
              placeholder="请输入模型训练参数"
              v-model="input2"
              clearable
              style="position: absolute; width: 20%; left: 46%; top: 32%"
            >
            </el-input>
            <el-row style="position: absolute; left: 17%; top: 26%">
              <el-button type="primary" round @click="submitForm"
                >提交参数</el-button
              >
            </el-row>
            <span
              style="position: absolute; left: 46%; top: 50%; color: #606266"
            >
              开始训练模型:
            </span>
            <el-row style="position: absolute; left: 60%; top: 48%">
              <el-button
                type="primary"
                round
                @click="startTraining"
                class="custom-button"
                :loading="startTrainingLoading"
                :disabled="startTrainingLoading"
              >
                <span v-if="startTrainingLoading">
                  <i class="el-icon-loading"></i>
                  <span class="loading-text">拼命训练中...</span>
                </span>
                <span v-else>开始训练</span>
              </el-button>
            </el-row>
            <span
              style="color: #606266; top: 70%; left: 10%; position: absolute"
              >下载训练模型:</span
            >
            <el-button
              type="text"
              style="top: 69%; left: 22%; position: absolute"
              @click="open"
              >点击下载训练模型 Message Box</el-button
            >
          </div>
          <!--测试-->
          <div class="test">
            <el-tag type="success">测试</el-tag>
            <span
              style="color: #606266; top: 110%; left: 10%; position: absolute"
              >上传测试文件：</span
            >
            <el-upload
              style="position: absolute; left: 23%; top: 109%"
              action="https://jsonplaceholder.typicode.com/posts/"
              :http-request="onUpload2"
            >
              <el-button size="small" type="primary">点击上传</el-button>
            </el-upload>

            <span
              style="position: absolute; left: 46%; top: 110%; color: #606266"
            >
              开始测试数据:
            </span>
            <el-row style="position: absolute; left: 60%; top: 109%">
              <el-button
                type="primary"
                round
                @click="testTraining"
                class="custom-button"
                :loading="testTrainingLoading"
                :disabled="testTrainingLoading"
              >
                <span v-if="testTrainingLoading">
                  <i class="el-icon-loading"></i>
                  <span class="loading-text">拼命测试中...</span>
                </span>
                <span v-else>开始测试</span>
              </el-button>
            </el-row>

            <span
              style="color: #606266; top: 130%; left: 10%; position: absolute"
              >测试结果下载:</span
            >
            <el-button
              type="text"
              style="top: 129%; left: 22%; position: absolute"
              @click="open2"
              >点击下载测试结果 Message Box</el-button
            >
          </div>
          <!--验证-->
          <div class="verify">
            <el-tag type="success">验证</el-tag>
            <span
              style="color: #606266; top: 170%; left: 10%; position: absolute"
              >上传验证集文件：</span
            >
            <el-upload
              style="position: absolute; left: 25%; top: 169%"
              action="https://jsonplaceholder.typicode.com/posts/"
              :http-request="onUpload3"
            >
              <el-button size="small" type="primary">点击上传</el-button>
            </el-upload>
            <span
              style="position: absolute; left: 46%; top: 170%; color: #606266"
            >
              开始验证结果:
            </span>
            <el-row style="position: absolute; left: 60%; top: 169%">
              <el-button
                type="primary"
                round
                @click="verifyTraining"
                class="custom-button"
                :loading="verifyTrainingLoading"
                :disabled="verifyTrainingLoading"
              >
                <span v-if="verifyTrainingLoading">
                  <i class="el-icon-loading"></i>
                  <span class="loading-text">拼命验证中...</span>
                </span>
                <span v-else>开始验证</span>
              </el-button>
            </el-row>
            <span
              style="color: #606266; top: 190%; left: 10%; position: absolute"
              >验证结果下载:</span
            >
            <el-button
              type="text"
              style="top: 189%; left: 22%; position: absolute"
              @click="open3"
              >点击下载验证结果 Message Box</el-button
            >
          </div>
          <div style="clear: both"></div>
        </div>
      </el-card>
    </div>
     
  </div>
</template>
<style scoped>
#background {
  background-color: rgba(0, 238, 255, 0.64);
  position: fixed;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  background-image: linear-gradient(90deg, cyan, purple);
  background-size: 400%;
  animation: myanimation 10s infinite;
}
@keyframes myanimation {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

.el-card {
  color: aliceblue;
  width: 1000px;
  top: 0%;
  left: 32%;
  height: 100%;
  background-color: rgba(255, 255, 2555, 0.7);
  border-radius: 30px;
  position: absolute;
}
.el-card .picture-card {
  color: aliceblue;
  width: 1000px;
  top: 0%;
  left: 0%;
  height: 40%;
  background-color: rgba(255, 255, 2555, 0.2);
  border-radius: 0px;
}
/* .el-card.picture-card[data-v-649dc84e] {
  font-size: 500px;
  color: aliceblue;

} */
.demo-image__lazy {
  height: 370px;
  width: 1000px;
  left: 0%;
  overflow-y: auto;
}
.ddemo-image__lazy .el-image {
  display: block;
  min-height: 100px;
  margin-bottom: 10px;
}
.container {
  height: 60%;
  width: 1000px;
  bottom: 0%;
  left: 0%;
  position: absolute;
  overflow: auto;
}
.container .train {
  border: 1px solid aliceblue;
  height: 90%;
  width: 1000px;
  left: 0%;
  top: 40%;

}
.container .test {
  border: 1px solid aliceblue;
  height: 60%;
  width: 1000px;

}
.container .verify {
  border: 1px solid aliceblue;

  height: 60%;
  width: 1000px;
}

#traintitle {
  color: black;
  font-size: 40px;
  top: 0%;
  left: 0%;
  position: absolute;
}
#testtitle {
  color: #303133;
  font-size: 40px;
  top: 103%;
  left: 0%;
  position: absolute;
}
#verifytitle {
  color: #303133;
  font-size: 40px;
  top: 176%;
  left: 0%;
  position: absolute;
}
#knn {
  color: #606266;
  top: 21%;
  left: 30%;
  position: absolute;
}
#moxing {
  color: #606266;
  left: 30%;
  top: 33%;
  position: absolute;
}
.el-tag {
  height: 50px;
  width: 100px;
  font-size: 20px;
}

.custom-button .loading-text {
  margin-left: 5px;
}
.custom-button .el-icon-loading {
  animation: spin 2s infinite linear;
}
@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
#logo{
    color: aliceblue;
    font-size: 25px;
    font-weight: 800;
    position: absolute;
    top: 15%;
    left: 15%;


}
#clock{
    color: aliceblue;
    position: absolute;
    top: 70%;
    left: 10%;
    text-align: left;
}
#time{
    font-size: 100px;
}
#date{
    font-size: 75px;
}
</style>
</style>
<script>
import { postUpload } from "@/router";
import { MessageBox } from "element-ui";

// 文件上传

export default {
  data() {
    return {
      time:"",
      date:"",
      hasError1: false,
      hasError2: false,
      hasError3: false,
      软件杯_testresult_RandomForest:
        "static/file/软件杯_testresult_RandomForest.txt",
      startTrainingLoading: false,
      testTrainingLoading: false,
      verifyTrainingLoading: false,
      input: 103,
      input2: 0.04,
      urls: [
        "http://127.0.0.1:8000/plot/",
        "http://127.0.0.1:8000/plot2/",
        "http://127.0.0.1:8000/plot1/",
      ],
      downloadLink: "",
      downloadLink1: "",
      downloadLink2: "", // 保存文件的下载链接
    };
  },
  mounted() {
    // 每隔一段时间刷新图片展示区域
    setInterval(() => {
      this.refreshImages();
    }, 10000);
    this.$nextTick(()=>{
            setInterval(this.update_clock,10)
        }

        ) // 5秒钟刷新一次，可以根据需要调整时间间隔
  },
  methods: {
    update_clock:function(){
            var d=new Date();
            var year=d.getFullYear();
            if(year<10){
                year="0"+year;
            }
            var day=d.getDate();
            if(day<10){
                day="0"+day;
            }
            var mon=d.getMonth()+1;
            if(mon<10){
                mon="0"+mon;
            }
            var hour=d.getHours();
            if(hour<10){
                hour="0"+hour;
            }
            var minute=d.getMinutes();
            if(minute<10){
                minute="0"+minute;
            }
            this.time=hour+":"+minute;
            this.date=year+":"+mon+":"+day;

        },
    refreshImages() {
      // 重新设置图片URL，强制刷新图片展示区域
      this.urls = this.urls.map((url) => `${url}?timestamp=${Date.now()}`);
    },

    //模型训练
    startTraining() {
      let hasError1 = false; // 标志变量，初始值为 false
      this.startTrainingLoading = true; // 显示开始训练的加载状态
      this.$axios
        .post("http://127.0.0.1:8000/train/")
        .then((response) => {
          const filePath = response.data.filePath; // 假设后端返回文件的路径
          this.downloadLink = filePath; // 将文件路径保存到前端数据中
        })
        .catch((error) => {
          console.error(error);
          MessageBox.alert("文件不存在(请检查是否上传了验证集文件)", "错误", {
            confirmButtonText: "确定",
            type: "error",
          });
          hasError1 = true; // 设置标志变量为 true，表示存在错误
        })
        .finally(() => {
          this.startTrainingLoading = false; // 隐藏开始训练的加载状态
          if (!hasError1) {
            MessageBox.alert("训练成功！", "成功", {
              confirmButtonText: "确定",
              type: "success",
            });
          }
        });
    },
    //测试数据
    testTraining() {
      let hasError2 = false; // 标志变量，初始值为 false
      this.testTrainingLoading = true; // 显示开始测试的加载状态
      this.$axios
        .post("http://127.0.0.1:8000/test/")
        .then((response) => {
          const filePath = response.data.filePath; // 假设后端返回文件的路径
          this.downloadLink1 = filePath; // 将文件路径保存到前端数据中
        })
        .catch((error) => {
          console.error(error);
          MessageBox.alert("文件不存在(请检查是否上传了测试集文件)", "错误", {
            confirmButtonText: "确定",
            type: "error",
          });
          hasError2 = true; // 设置标志变量为 true，表示存在错误
        })
        .finally(() => {
          this.testTrainingLoading = false; // 隐藏开始训练的加载状态
          if (!hasError2) {
            MessageBox.alert("测试成功！", "成功", {
              confirmButtonText: "确定",
              type: "success",
            });
          }
        });
    },
    //验证结果
    verifyTraining() {
      let hasError3 = false; // 标志变量，初始值为 false
      (this.verifyTrainingLoading = true),
        this.$axios
          .post("http://127.0.0.1:8000/verify/")
          .then((response) => {
            const filePath = response.data.filePath; // 假设后端返回文件的路径
            this.downloadLink2 = filePath; // 将文件路径保存到前端数据中
          })
          .catch((error) => {
            console.error(error);
            MessageBox.alert("文件不存在(请检查是否上传了验证集文件)", "错误", {
              confirmButtonText: "确定",
              type: "error",
            });
            hasError3 = true; // 设置标志变量为 true，表示存在错误
          })
          .finally(() => {
            this.verifyTrainingLoading = false; // 隐藏开始训练的加载状态
            if (!hasError3) {
              MessageBox.alert("验证成功！", "成功", {
                confirmButtonText: "确定",
                type: "success",
              });
            }
          });
    },
    downloadFile(filePath) {
      // 根据文件路径进行文件下载操作
      // 可以使用下载链接或其他方式实现文件下载
    },
    //提交参数
    submitForm() {
      // 在这里发送请求到后端，将输入框的值作为请求的一部分
      const data = {
        kValue: this.input,
        kValue2: this.input2,
      };

      // 使用 Axios 或其他 HTTP 客户端库发送请求
      this.$axios
        .post("http://127.0.0.1:8000/canshu/", data)
        .then((response) => {
          // 请求成功的处理逻辑
          console.log(response.data);
          // 在这里进行请求成功后的操作，例如显示成功提示，刷新数据等
          // 示例：显示成功消息
          this.$message.success("请求成功");
        })
        .catch((error) => {
          // 请求失败的处理逻辑
          console.error(error);
          // 在这里进行请求失败后的操作，例如显示错误提示，回滚操作等
          // 示例：显示错误消息
          this.$message.error("请求失败");
        });
    },
    //提交文件
    // 上传文件
    // 文件上传
    onUpload1(file) {
      let formData = new FormData();
      formData.append("file", file.file);
      postUpload(formData, "http://127.0.0.1:8000/upload1/")
        .then((res) => {
          console.log(res);
          this.$message.success("UPLOAD_SUCCESS");
        })
        .catch((e) => {
          this.$message.error(e.message);
        });
    },
    onUpload2(file) {
      let formData = new FormData();
      formData.append("file", file.file);
      postUpload(formData, "http://127.0.0.1:8000/upload2/")
        .then((res) => {
          console.log(res);
          this.$message.success("UPLOAD_SUCCESS");
        })
        .catch((e) => {
          this.$message.error(e.message);
        });
    },
    onUpload3(file) {
      let formData = new FormData();
      formData.append("file", file.file);
      postUpload(formData, "http://127.0.0.1:8000/upload3/")
        .then((res) => {
          console.log(res);
          this.$message.success("UPLOAD_SUCCESS");
        })
        .catch((e) => {
          this.$message.error(e.message);
        });
    },
    open() {
      this.$axios
        .get("http://127.0.0.1:8000/download_model/", { responseType: "blob" })
        .then((response) => {
          const url = window.URL.createObjectURL(new Blob([response.data]));
          const link = document.createElement("a");
          link.href = url;
          link.setAttribute("download", "decision_tree_model.joblib");
          document.body.appendChild(link);
          link.click();
        })
        .catch((error) => {
          console.error(error);
          // 处理错误情况
          MessageBox.alert(
            "文件不存在(请检查是否上传了训练集文件或者是否已经训练)",
            "错误",
            {
              confirmButtonText: "确定",
              type: "error",
            }
          );
        });
    },
    open2() {
      this.$axios
        .get("http://127.0.0.1:8000/download_test/", { responseType: "blob" })
        .then((response) => {
          const url = window.URL.createObjectURL(new Blob([response.data]));
          const link = document.createElement("a");
          link.href = url;
          link.setAttribute("download", "preddata.json");
          document.body.appendChild(link);
          link.click();
        })
        .catch((error) => {
          console.error(error);
          // 处理错误情况
          MessageBox.alert(
            "文件不存在(请检查是否上传了测试集文件或者是否已经测试)",
            "错误",
            {
              confirmButtonText: "确定",
              type: "error",
            }
          );
        });
    },
    open3() {
      this.$axios
        .get("http://127.0.0.1:8000/download_verify/", { responseType: "blob" })
        .then((response) => {
          const url = window.URL.createObjectURL(new Blob([response.data]));
          const link = document.createElement("a");
          link.href = url;
          link.setAttribute("download", "软件杯_testresult_RandomForest.txt");
          document.body.appendChild(link);
          link.click();
        })
        .catch((error) => {
          console.error(error);
          // 处理错误情况
          MessageBox.alert(
            "文件不存在(请检查是否上传了验证集文件或者是否已经验证)",
            "错误",
            {
              confirmButtonText: "确定",
              type: "error",
            }
          );
        });
    },
  },

  // mounted() {
  //   this.loadPlot(); // 页面加载完成后调用加载图片的方法
  // }
};
</script>
